.team {
  position: relative;
  background: $main-light-color;
  height: 75.37%;
  padding: 39px 55px;
  box-sizing: border-box;
  width: 100%;

  &_top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 69px;
  }

  &_logo {
    color: #d2132a;
    font-size: 32px;
  }

  &_callback {
    display: flex;
    align-items: center;
  }

  &_circle {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 54px;
    height: 54px;
    border-radius: 50%;
    background-color: $color-white;
  }

  &_circles {
    width: 18px;
    height: 18px;
  }

  &_contacts {
    font-size: 12px;
    font-family: "Archivo", sans-serif;
    font-style: normal;
    font-weight: bold;
    line-height: 13px;
    text-decoration: none;
    text-transform: uppercase;
    color: #161415;
    margin-right: 22px;
  }

  &_figures_item {
    position: absolute;
    z-index: 2;
  }

  &_figures_item-sphere {
    bottom: 12%;
    left: 18%;
  }

  &_figures_item-note {
    left: 60%;
    top: 25%;
  }

  &_figures_item-torus {
    bottom: 5%;
    right: 18%;
  }

  &_headline {
    font-family: "Archivo Black", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 40px;
    line-height: 40px;
    text-align: center;
    text-transform: uppercase;
    color: #383030;
    margin-bottom: 50px;
  }

  &_slider {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &_slider_wrapper {
    display: flex;
    overflow: hidden;
    width: 1070px;
    position: relative;
    margin: 0 49px;
  }

  &_list {
    list-style: none;
    display: flex;
    transition: transform 0.5s;
  }

  &_item {
    position: relative;
    width: 263px;
    height: 335px;
    margin-right: 6px;
  }

  &_image {
    border-radius: 5px;
  }

  &_item-hover {
    display: none;
    flex-direction: column;
    box-sizing: border-box;
    padding-top: 142px;
    width: 100%;
    height: 100%;
    text-align: center;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    top: 0;
    position: absolute;
    color: $color-white;
    background: $main-hover-gradient;
    opacity: 0.9;
    border-radius: 5px;
  }

  &_item-hover_paragraph {
    width: 148px;
    font-family: "Archivo Black", sans-serif;
    line-height: 17px;
    letter-spacing: 0.42px;
    text-transform: uppercase;
    margin: 0 auto;
  }

  &_item-hover_link {
    font-family: "Archivo", sans-serif;
    line-height: 13px;
    color: inherit;
    text-decoration: none;
    margin-top: 10px;

  }

  &_button-prev,
  &_button-next {
    width: 76px;
    height: 76px;
    outline: none;
    border: none;
    background: none;
    border-radius: 50%;
    font-family: "Archivo", sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 13px;
    text-transform: uppercase;
    color: #161415;
  }

  &_button-prev:hover,
  &_button-next:hover {
    background: $main-hover-gradient;
    color: $color-white;
  }
}

@media only screen and(max-width: $medium-monitor) {
  .team {
    &_top {
      margin-bottom: 20px;
    }
  }
}

@media only screen and(max-width: 1280px) {
  .team {
    &_top {
      margin-bottom: 20px;
    }

    &_slider_wrapper {
      width: 801px;
    }
  }
}

@media only screen and(max-width: $low-monitor) {
  .team {

    &_slider_wrapper {
      width: 532px;
    }
  }
}

@media only screen and(max-width: $medium-tablet) {
  .team {

    &_slider_wrapper {
      width: 263px;
    }
  }
}

@media only screen and(max-width: $low-tablet) {
  .team {
    padding: 20px 0;

    &_top {
      display: none;
    }

    &_figures {
      display: none;
    }

    &_slider_wrapper {
      margin: 0;
    }
  }
}

@media only screen and(max-width: 375px) {
  .team {

    &_headline {
      margin-bottom: 10px;
    }

    &_slider_wrapper {
      width: 220px;
    }
    
    &_item {
      width: 220px;
    }

    &_button-prev:hover,
    &_button-next:hover {
      background: none;
      color: #383030;
    }

    &_image {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }
}

@media only screen and(max-width: 320px) {
  .team {
    &_slider_wrapper {
      width: 180px;
    }
    
    &_item {
      width: 180px;
    }
  }
}
